@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&family=Orbitron:wght@400;700;900&family=Rajdhani:wght@400;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        --background: 0 0% 1%;
        --foreground: 0 0% 93%;
        --card: 0 0% 5%;
        --card-foreground: 0 0% 93%;
        --popover: 0 0% 5%;
        --popover-foreground: 0 0% 93%;
        --primary: 180 100% 50%;
        --primary-foreground: 0 0% 0%;
        --secondary: 300 100% 50%;
        --secondary-foreground: 0 0% 100%;
        --muted: 0 0% 10%;
        --muted-foreground: 0 0% 60%;
        --accent: 270 100% 50%;
        --accent-foreground: 0 0% 100%;
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 20%;
        --input: 0 0% 15%;
        --ring: 180 100% 50%;
        --radius: 0.5rem;
    }
}

html {
    color-scheme: dark;
}

body {
    margin: 0;
    font-family: 'Rajdhani', 'Inter', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #030303;
    color: #EDEDED;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Orbitron', sans-serif;
    font-weight: 700;
    letter-spacing: 0.05em;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@layer base {
    * {
        @apply border-border;
    }
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #0a0a0a;
}

::-webkit-scrollbar-thumb {
    background: #00FFFF30;
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: #00FFFF50;
}

/* Neon glow effects */
.neon-cyan {
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.7), 0 0 20px rgba(0, 255, 255, 0.5), 0 0 30px rgba(0, 255, 255, 0.3);
}

.neon-magenta {
    text-shadow: 0 0 10px rgba(255, 0, 255, 0.7), 0 0 20px rgba(255, 0, 255, 0.5), 0 0 30px rgba(255, 0, 255, 0.3);
}

.border-glow-cyan {
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.15), inset 0 0 15px rgba(0, 255, 255, 0.05);
}

.border-glow-magenta {
    box-shadow: 0 0 15px rgba(255, 0, 255, 0.15), inset 0 0 15px rgba(255, 0, 255, 0.05);
}

/* Scanlines overlay */
.scanlines::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: repeating-linear-gradient(
        0deg,
        transparent,
        transparent 2px,
        rgba(0, 0, 0, 0.1) 2px,
        rgba(0, 0, 0, 0.1) 4px
    );
    pointer-events: none;
    z-index: 10;
}

/* Glitch effect */
@keyframes glitch {
    0% {
        transform: translate(0);
    }
    20% {
        transform: translate(-2px, 2px);
    }
    40% {
        transform: translate(-2px, -2px);
    }
    60% {
        transform: translate(2px, 2px);
    }
    80% {
        transform: translate(2px, -2px);
    }
    100% {
        transform: translate(0);
    }
}

.glitch:hover {
    animation: glitch 0.3s ease-in-out;
}

/* Flicker animation */
@keyframes flicker {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.8; }
}

.flicker {
    animation: flicker 3s ease-in-out infinite;
}

/* Cyber button */
.cyber-btn {
    position: relative;
    clip-path: polygon(0 0, 100% 0, 100% 70%, 90% 100%, 0 100%);
    transition: background-color 0.3s, color 0.3s, transform 0.2s;
}

.cyber-btn:hover {
    transform: scale(1.02);
}

.cyber-btn::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10%;
    height: 30%;
    background: currentColor;
    clip-path: polygon(100% 0, 0 100%, 100% 100%);
    opacity: 0.5;
}

/* Card hover effect */
.cyber-card {
    transition: border-color 0.3s, transform 0.3s, box-shadow 0.3s;
}

.cyber-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 0 30px rgba(0, 255, 255, 0.1);
}

/* Input focus states */
input:focus, textarea:focus, select:focus {
    outline: none;
    border-color: #00FFFF !important;
    box-shadow: 0 0 10px rgba(0, 255, 255, 0.2);
}

/* Status badges */
.status-pending { background-color: rgba(255, 193, 7, 0.2); color: #FFC107; border: 1px solid #FFC107; }
.status-processing { background-color: rgba(0, 255, 255, 0.2); color: #00FFFF; border: 1px solid #00FFFF; }
.status-shipped { background-color: rgba(138, 43, 226, 0.2); color: #8A2BE2; border: 1px solid #8A2BE2; }
.status-completed { background-color: rgba(0, 255, 0, 0.2); color: #00FF00; border: 1px solid #00FF00; }
.status-cancelled { background-color: rgba(255, 0, 0, 0.2); color: #FF0000; border: 1px solid #FF0000; }

/* Pulse animation for cart icon */
@keyframes pulse-cyan {
    0%, 100% { box-shadow: 0 0 0 0 rgba(0, 255, 255, 0.7); }
    50% { box-shadow: 0 0 0 10px rgba(0, 255, 255, 0); }
}

.pulse-cyan {
    animation: pulse-cyan 2s infinite;
}

/* Debug wrapper fix */
[data-debug-wrapper="true"] {
    display: contents !important;
}
