/* Main app styles */
.app-container {
    min-height: 100vh;
    background-color: #030303;
}

/* Hero section */
.hero-section {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.hero-bg {
    position: absolute;
    inset: 0;
    background: radial-gradient(ellipse at center, rgba(0, 255, 255, 0.05) 0%, transparent 70%),
                radial-gradient(ellipse at 80% 20%, rgba(255, 0, 255, 0.05) 0%, transparent 50%);
}

/* Navigation */
.nav-link {
    position: relative;
    color: #888;
    transition: color 0.3s;
}

.nav-link:hover {
    color: #00FFFF;
}

.nav-link::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg, #00FFFF, #FF00FF);
    transition: width 0.3s;
}

.nav-link:hover::after {
    width: 100%;
}

/* Product grid */
.product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.5rem;
}

/* Service card bento grid */
.bento-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 250px);
    gap: 1rem;
}

.bento-grid > *:nth-child(1) {
    grid-column: span 2;
    grid-row: span 2;
}

.bento-grid > *:nth-child(2) {
    grid-column: span 2;
}

.bento-grid > *:nth-child(3) {
    grid-column: span 1;
}

.bento-grid > *:nth-child(4) {
    grid-column: span 1;
}

@media (max-width: 1024px) {
    .bento-grid {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
    }
    .bento-grid > *:nth-child(1) {
        grid-column: span 2;
        grid-row: span 1;
    }
    .bento-grid > *:nth-child(n) {
        grid-column: span 1;
    }
}

@media (max-width: 640px) {
    .bento-grid {
        grid-template-columns: 1fr;
    }
    .bento-grid > *:nth-child(n) {
        grid-column: span 1;
    }
}

/* Cart badge */
.cart-badge {
    position: absolute;
    top: -8px;
    right: -8px;
    background: #FF00FF;
    color: white;
    font-size: 12px;
    font-weight: bold;
    min-width: 20px;
    height: 20px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Order status indicator */
.status-indicator {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.status-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

.status-dot.pending { background: #FFC107; }
.status-dot.processing { background: #00FFFF; }
.status-dot.shipped { background: #8A2BE2; }
.status-dot.completed { background: #00FF00; }
.status-dot.cancelled { background: #FF0000; }

/* Form styles */
.cyber-input {
    background: rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: white;
    padding: 0.75rem 1rem;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.cyber-input:focus {
    border-color: #00FFFF;
    box-shadow: 0 0 10px rgba(0, 255, 255, 0.2);
}

/* Loading spinner */
@keyframes spin {
    to { transform: rotate(360deg); }
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 3px solid rgba(0, 255, 255, 0.1);
    border-top-color: #00FFFF;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

/* Fade in animation */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

.fade-in {
    animation: fadeIn 0.5s ease-out forwards;
}

/* Stagger children animation */
.stagger-children > * {
    opacity: 0;
    animation: fadeIn 0.5s ease-out forwards;
}

.stagger-children > *:nth-child(1) { animation-delay: 0.1s; }
.stagger-children > *:nth-child(2) { animation-delay: 0.2s; }
.stagger-children > *:nth-child(3) { animation-delay: 0.3s; }
.stagger-children > *:nth-child(4) { animation-delay: 0.4s; }
.stagger-children > *:nth-child(5) { animation-delay: 0.5s; }
.stagger-children > *:nth-child(6) { animation-delay: 0.6s; }

/* Responsive text */
@media (max-width: 640px) {
    h1 { font-size: 2rem; }
    h2 { font-size: 1.5rem; }
}
